import ShoppingForm from "./ShoppingForm";

export default function Steps({ cartItems, activeStep, shippingForm }: any) {
  return (
    <div
      className="w-full lg:w-7/12 shadow-lg border-1 border-amber-100 p-8 rounded-lg flex *:
          
          flex-col gap-8"
    >
      {activeStep === 1 ? (
        cartItems.map((item) => {
          // eslint-disable-next-line react/jsx-key
          return <ProductCard key={item.id} item={item} />;
        })
      ) : activeStep === 2 ? (
        <ShoppingForm />
      ) : activeStep === 3 && shippingForm ? (
        <PaymentForm />
      ) : (
        <p className="text-sm text-gray-400">
          Please fill the shipping form first
        </p>
      )}
    </div>
  );
}
